/* General styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #ffffff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  position: relative; /* Ensure relative positioning for the hamburger */
}

.logo {
  width: 120px; /* Adjust logo size */
  height: auto;
}

/* Ensure hamburger button is visible */
.hamburger {
  display: none; /* Hidden by default */
}

.nav-links {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 20px;
}

.nav-links li {
  font-size: 16px;
  cursor: pointer;
}

.join-button {
  padding: 8px 16px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.join-button:hover {
  background-color: #0056b3;
}

/* Responsive styles for small screens */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .logo {
    margin-bottom: 10px; /* Add spacing between logo and hamburger */
  }

  /* Display the hamburger button for smaller screens */
  .hamburger {
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin-left: auto;
    gap: 5px;
  }

  .hamburger div {
    width: 25px;
    height: 3px;
    background-color: black;
  }

  .nav-links {
    flex-direction: column;
    gap: 10px;
    width: 100%;
    display: none; /* Initially hide menu */
  }

  .nav-links.active {
    display: flex; /* Show menu when active */
  }

  .join-button {
    margin-top: 10px;
    width: 50%;
  }
}
